'use client';

import { useEffect } from 'react';

export default function PageAnimScript() {
  useEffect(() => {
    const pageRoot = document.getElementById('__page_root');
    if (pageRoot) {
      requestAnimationFrame(() => {
        pageRoot.classList.add('page-anim--enter');
        pageRoot.setAttribute('data-loaded', 'true');
      });
    }
  }, []);

  return null;
}
